<template>
    <div>
        <header class="header" @click="toSerch" >
             <van-search
                placeholder="请输入搜索编程单词"
                show-action    
            >
            <van-button slot="action" size="small" type="default">搜索</van-button>
             </van-search>
        </header>
        <div class="content">
            <ul>
                <header class="c-heaer">
                    <h2>
                        综合咨询
                    </h2>
                    <ul class="h-right">
                        <li @click="conActive=1" :class="[conActive==1?'hr-on':'']" ></li>
                        <li @click="conActive=2" :class="[conActive==2?'hr-on':'']"></li>
                        <li @click="conActive=3" :class="[conActive==3?'hr-on':'']"></li>  
                    </ul>
                   
                </header>
                <div class="c-active">
                    <ul>
                        <li v-for="(item,index) in sliceConDatas " :key="index" @click="toContent(item.id)" >
                         <p> <b></b> {{item.title}}</p>
                        </li>
                    </ul>
                </div>
                
            </ul>

            <ul>
                <header class="c-heaer">
                    <h2>
                        每日编程
                    </h2>
                    <ul class="h-right">
                        <li @click="proActive=1" :class="[proActive==1?'hr-on':'']" ></li>
                        <li @click="proActive=2" :class="[proActive==2?'hr-on':'']"></li>
                        <li @click="proActive=3" :class="[proActive==3?'hr-on':'']"></li>  
                    </ul>
                   
                </header>
                <div class="c-active">
                    <ul>
                        <li v-for="(item,index) in sliceProDatas " :key="index" @click="toContent(item.id)" >
                         <p> <b></b> {{item.title}}</p>
                        </li>
                    </ul>
                </div>
                
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            //综合咨询tab切换
            conActive:1,
            conDatas:[],
            //每日编程
            proActive:1,
            proDatas:[]

        }
    },
    created(){
        this.conGetDailyList();

        this.proGetDailyList()
    },

    computed:{
        sliceConDatas(){
            let arr = []
     
            if (this.conActive==1) {
                arr = this.conDatas.slice(0,10)
            }else if (this.conActive == 2) {
                arr = this.conDatas.slice(10,20)
            }else{
                arr = this.conDatas.slice(20,30)
            }
            
            return arr;
        },

        sliceProDatas(){
            let arr = []
     
            if (this.proActive==1) {
                arr = this.proDatas.slice(0,10)
            }else if (this.proActive == 2) {
                arr = this.proDatas.slice(10,20)
            }else{
                arr = this.proDatas.slice(20,30)
            }
            
            return arr;
        }
    },
    methods:{
        // 获取获取首页列表：
      async conGetDailyList(){
        let data = await this.API.conGetDailyLists();
        if (!data.error) {
            this.conDatas = data.data;
        }
    
      },

      async proGetDailyList(){
          let data = await this.API.proGetDailyLists();
          if (!data.error) {
              this.proDatas = data.data;
          }
   
      },

      toContent(id){
           this.$router.push({
              path:'/indexContent',
              query:{
                  id,
              }
          })
      },

      toSerch(){
          this.$router.push({
              path:'/indexSerch'
          })
      }
    }
}
</script>


